export function initGraph(wrapper) {
  return new Graph({
    container: document.getElementById(wrapper),
    width: "100%",
    height: "100%",
    autoResize: true,
    grid: {
      visible: true,
      type: "doubleMesh", // 
      args: [
        {
          color: "#ddd", // 
          thickness: 1, // 
        },
        {
          color: "#eee", // 
          thickness: 1, // 
          factor: 4, // 
        },
      ],
    },
    background: {
      color: "#F2F7FA",
    },
    // 
    resizing: {
      enabled: true,
    },
    mousewheel: {
      enabled: true,
      modifiers: ["ctrl", "meta"],
    },
    panning: true,
    interacting: true, //
    snapline: true, // 
  });
}

// 
export function initPlugIns(graph) {
  graph.use(
    new Transform({
      // resizing: true,
      // rotating: true,
    })
  );
  graph.use(
    new Keyboard({
      enabled: true,
    })
  );
  graph.use(
    new History({
      enabled: true,
    })
  );
  graph.use(
    new Selection({
      enabled: true,
      showNodeSelectionBox: true,
      pointerEvents: "none",
    })
  );
  graph.setSelectionFilter((cell) => {
    return cell.shape !== "car-node";
  });
}

// 
export function initStencil(graph) {
  return new Stencil({
    title: "",
    target: graph,
    groups: [
      {
        name: "",
        graphHeight: 200,
      },
      {
        name: "",
        graphHeight: 300,
        // layoutOptions: {
        //   columns: 4,
        //   dx: 1,
        //   marginX: 1,
        // },
        // layout: (model) => {
        //   console.log(model);
        //   const nodes = model.collection.cells;
        //   nodes.forEach((item, index) => {
        //     item.position(
        //       (index % 4) * 50,
        //       20 * (Math.floor(index / 4) * 2 + 1)
        //     );
        //   });
        // },
      },
    ],
    layoutOptions: { rowHeight: 50 },
    getDropNode: (node) => {
      const { width, height } = node.size();
      if (node.shape === "custom-node") {
        node.attr("text/fontSize", 10);
        node.attr("circle/r", 3);
        return node.clone();
      }
      return node.clone().size(width, height);
    },
  });
}

